import React, { useEffect, useState } from 'react'
import {
  AppBar,
  Button,
  createStyles,
  Grid,
  makeStyles,
  Slide,
  Theme,
  Zoom,
  Toolbar,
  Typography,
  Fab,
  useScrollTrigger
} from '@material-ui/core'
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'
import { Link } from 'react-scroll'

interface IProps {
  window?: () => Window
  children: React.ReactElement
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      position: 'fixed',
      bottom: theme.spacing(2),
      right: theme.spacing(2)
    }
  })
)

const HideOnScroll = (props: IProps) => {
  const trigger = useScrollTrigger()
  return (
    <Slide appear={true} style={{ boxShadow: 'inherit' }} direction='down' in={!trigger}>
      {props.children}
    </Slide>
  )
}

const ScrollTop = (props: IProps) => {
  const classes = useStyles();
  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 100,
  })

  const handleClick = (event: React.MouseEvent<HTMLDivElement>) => {
    const anchor = ((event.target as HTMLDivElement).ownerDocument || document)
      .querySelector('#home',)

    if (anchor) anchor.scrollIntoView({ behavior: 'smooth', block: 'center' })
  }

  return (
    <Zoom in={trigger}>
      <div onClick={handleClick} role="presentation" className={classes.root}>
        {props.children}
      </div>
    </Zoom>
  )
}

const Navbar = (props: IProps) => {
  const [show, setShow] = useState(false)

  useEffect(() => {
    window.onscroll = () => {
      if (window.scrollY > 500)
        setShow(true)
      else
        setShow(false)
    }
  }, [])


  return (
    <React.Fragment>
      <HideOnScroll>
        <AppBar style={{
          boxShadow: show ? 'inherit' : 'none',
          backgroundColor: show ? '#0E2A47' : 'transparent'
        }}>
          <Toolbar>
            <Grid
              container
              style={{ margin: '0 auto', width: '80%', padding: '20px' }}
              justifyContent="space-between">
              <Grid item>
                <Typography variant="h6" style={{ color: 'white' }}>Luca Mienert</Typography>
              </Grid>
              <Grid item>
                <Link className='link-class' to='about' smooth={true} duration={1000}>
                  <Button
                    size='medium'
                    style={{ marginLeft: 10, background: 'transparent', color: 'white' }}>
                    Über mich
                  </Button>
                </Link>
                <Link className='link-class' to='skills' smooth={true} duration={1000}>
                  <Button
                    size='medium'
                    style={{ marginLeft: 10, background: 'transparent', color: 'white' }}>
                    Skills
                  </Button>
                </Link>
                <Link className='link-class' to='projects' smooth={true} duration={1000}>
                  <Button
                    size='medium'
                    style={{ marginLeft: 10, background: 'transparent', color: 'white' }}>
                    Projekt
                  </Button>
                </Link>
              </Grid>
            </Grid>
          </Toolbar>
        </AppBar>
      </HideOnScroll>
      <ScrollTop {...props}>
        <Fab color="secondary" size="small" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
    </React.Fragment >
  )
}

export default Navbar