import React from 'react'

import { makeStyles } from '@material-ui/core/styles'

import {
  Button,
  Card,
  CardActionArea,
  CardContent,
  CardMedia,
  Typography,
  CardActions
} from '@material-ui/core'

const useStyles = makeStyles({
  root: {
    maxWidth: 345,
    margin: 'auto',
    marginTop: 50
  },
});

interface Props {
  imageUrl?: string
  title: string
  height: number
  alt: string
  header: string
  desc: string
  buttonLink: string
}

const Project: React.FC<Props> = ({ imageUrl, title, height, alt, header, desc, buttonLink }) => {
  const classes = useStyles();
  return (
    <Card className={classes.root}>
      <CardActionArea>
        {imageUrl !== undefined ?
          <CardMedia
            component='img'
            alt={alt}
            height={height}
            image={imageUrl}
            title={title} />
          :
          <div>Test</div>}
        <CardContent>
          <Typography gutterBottom variant='h5' component='h2'>
            {header}
          </Typography>
          <Typography variant='body2' color='textSecondary' component='p'>
            {desc}
          </Typography>
        </CardContent>
        <CardActions>
          <Button size='small' color='primary'>
            Github
          </Button>
          <Button size='small' color='primary'>
            Mehr
          </Button>
        </CardActions>
      </CardActionArea>
    </Card>
  )
}

export default Project